import React from 'react';
import { Link } from 'react-router-dom';
import { headerSettings } from '../../data/constants';
import { EmptyObject } from '../../types/types';

class Header extends React.Component<EmptyObject> {
  constructor(props: EmptyObject) {
    super(props);
  }
  render(): JSX.Element {
    return (
      <header className="header">
        <h1 className="header-title">{headerSettings.title}</h1>
        <div className="header-nav">
          <Link className="header-link" data-testid="homeLink" to={'/'}>
            {headerSettings.linkHome}
          </Link>
          <Link className="header-link" data-testid="aboutLink" to={'/about'}>
            {headerSettings.linkAbout}
          </Link>
        </div>
      </header>
    );
  }
}

export default Header;
