import React from 'react';
import Search from '../search/Search';
import { data } from '../../data/data';
import Card from '../card/Card';
import { EmptyObject, HomepageState } from '../../types/types';
import { welcome } from '../../data/constants';

class Homepage extends React.Component<EmptyObject, HomepageState> {
  constructor(props: EmptyObject) {
    super(props);
  }
  state: HomepageState = { currentData: data };
  render(): JSX.Element {
    return (
      <div className="homepage" data-testid="homePage">
        <h2 className="homepage-title">{welcome}</h2>
        <Search />
        <div className="cards-container">
          {this.state.currentData.map((el, i) => {
            return <Card {...el} key={i} />;
          })}
        </div>
      </div>
    );
  }
}

export default Homepage;
