import React from 'react';
import { aboutSettings } from '../../data/constants';
import { EmptyObject } from '../../types/types';

class AboutUs extends React.Component<EmptyObject> {
  constructor(props: EmptyObject) {
    super(props);
  }
  render(): JSX.Element {
    return (
      <div className="about" data-testid="aboutPage">
        <h2 className="about-title">{aboutSettings.title}</h2>
        <section className="about-container">
          <p className="about-content">{aboutSettings.describe}</p>
        </section>
      </div>
    );
  }
}

export default AboutUs;
