import React from 'react';
import { Link } from 'react-router-dom';
import { notFoundSettings } from '../../data/constants';
import { EmptyObject } from '../../types/types';

class NotFound extends React.Component<EmptyObject> {
  constructor(props: EmptyObject) {
    super(props);
  }
  render(): JSX.Element {
    return (
      <div className="not-found-page-container" data-testid="notFoundPage">
        <h2 className="not-found-page-title">{notFoundSettings.title}</h2>
        <Link className="link-not-found" to={'/'}>
          {notFoundSettings.link}
        </Link>
        <p className="not-found-404">{notFoundSettings.number}</p>
      </div>
    );
  }
}

export default NotFound;
