import React from 'react';
import { searchSettings } from '../../data/constants';
import { EmptyObject, SearchState } from '../../types/types';

export function setLocalStorage(value: string): void {
  localStorage.setItem('searchData', value);
  console.log(localStorage.getItem('searchData'));
}

class Search extends React.Component<EmptyObject, SearchState> {
  constructor(props: EmptyObject) {
    super(props);
  }
  state: SearchState = { searchValue: '' };
  componentDidMount(): void {
    const localDataSearch: string | null = localStorage.getItem('searchData');
    if (localDataSearch !== null) {
      this.setState({ searchValue: localDataSearch });
    } else this.setState({ searchValue: '' });
  }

  componentWillUnmount(): void {
    setLocalStorage(this.state.searchValue);
  }
  render(): JSX.Element {
    return (
      <div className="search">
        <form className="search-form">
          <input
            className="search-input"
            onChange={(e) => {
              this.setState({ searchValue: e.target.value });
            }}
            type="text"
            data-testid="test-input-id"
            placeholder={searchSettings.placeholder}
          ></input>
          <button className="search-btn" disabled>
            {searchSettings.button}
          </button>
        </form>
        <p className="search-text">
          {searchSettings.result}
          {this.state.searchValue}
        </p>
      </div>
    );
  }
}

export default Search;
