import React from 'react';
import { Data } from '../../types/types';

class Card extends React.Component<Data> {
  constructor(props: Data) {
    super(props);
  }
  render(): JSX.Element {
    return (
      <div className="card" data-testid="card-test-id">
        <h3 className="card-title">{this.props.name}</h3>
        <img className="card-img" src={this.props.img} alt={this.props.name} />
        <p className="card-price">{`${this.props.price}$`}</p>
        <p className="card-color">{this.props.color}</p>
        <p className="card-year">{this.props.year}</p>
      </div>
    );
  }
}

export default Card;
